//--------- VARIABLES ---------
$amarillo: #FFBF00
//--------- VARIABLES ---------

// --------- FORMATEO ---------
*
    margin: 0
    padding: 0
    box-sizing: border-box
    list-style: none
a
    color: $amarillo
a:link, a:visited, a:active
    text-decoration: none
// --------- FORMATEO ---------

// --------- FUENTES ---------
@font-face
    font-family: "Harry P"
    src: url('../fonts/Harry.TTF') format("truetype")
    font-weight: normal
    font-style: normal
@font-face
    font-family: 'Benne'
    src: url('../fonts/Benne.ttf') format("truetype")
    font-weight: normal
    font-style: normal
// --------- FUENTES ---------

// --------- HEADER ---------
header
    width: 100%
    height: 15vh
    padding: 1%
    background-image: url("../img/fondo_head.png")
    display: flex
    align-items: center
    justify-content: space-between
.nav_header
    width: 60%
.apartados_head
    display: flex
    align-items: center
    justify-content: space-between
    font-family: "Harry P"
    font-size: 2.7rem
.iconos_head
    width: 4%
    display: flex
    align-items: center
    justify-content: space-between
    font-size: 1.5rem

.logo
    width: 85px

.caratula
    width: 10%
.iconos
    color: $amarillo
// --------- HEADER ---------

// --------- FOOTER ---------
footer
    width: 100%
    height: 15vh
    padding: 1%
    background-image: url("../img/fondo_head.png")
    display: flex
    align-items: center
    justify-content: space-between
.nav_footer
    width: 60%
.apartados_foot
    display: flex
    align-items: center
    justify-content: space-between
    font-family: "Harry P"
    font-size: 2.7rem

.iconos_foot
    width: 5%
    display: flex
    align-items: center
    justify-content: space-between
    font-size: 1.5rem
// --------- FOOTER ---------

// --------- MAIN ---------
main
    padding: 3%
    background-image: url("../img/fondo_body.jpg")
    background-repeat: no-repeat
    background-attachment: fixed
// --------- MAIN ---------



